.footer-social {
  display: flex;
  gap: 6px;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-social__link {
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  list-style-type: none;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}
